.header {
    display: flex;
    column-gap: 3rem;

    @include respond(phone) {
        flex-direction: column;
        align-items: center;
        row-gap: 5rem;
    }
    
    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        @include respond(tab-land) {
            row-gap: 1rem;
        }

        @include respond(phone) {
            align-items: center;
            row-gap: 2rem;
        }


        &__title {
            font-size: 6.5rem;
            text-transform: uppercase;
            line-height: 5.5rem;

            @include respond(tab-land) {
                font-size: 5.5rem;
            }
        }

        &__text {
            font-size: 2.2rem;

            @include respond(phone) {
                text-align: center;
            }
        }

        &__button {
            width: fit-content;
            padding: 0.7rem 5rem;

            font-size: $default-font-size;
            color: #fff;
            
            cursor: pointer;

            border: 2px solid $accent;

            transition: all 0.1s linear;
            opacity: 0.8;

            &:hover {
                transform: scale(103%);
                opacity: 1;
            }
        }
    }
}
