.block_bg {
    background: linear-gradient(152.97deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
    border-radius: 2.5rem;
    
    border: .1rem solid;
    border-image-source: radial-gradient(54.8% 53% at 50% 50%, rgba(21, 21, 21, 0.6) 0%, rgba(21, 21, 21, 0) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
}

