.rules {
    width: 100%;

    &__wrapper {
        width: 100%;

        display: flex;
        justify-content: space-around;
        align-items: flex-start;
        flex-wrap: wrap;
        gap: 1.5rem;
        row-gap: 6rem;

        margin-top: 6rem;
    }

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        column-gap: 2rem;
        padding: 0px 2rem 0rem 2.5rem;

        width: 32%;
        min-height: 20rem;
        min-width: 33rem;
        
        backdrop-filter: blur(2px);

        &__number {
            font-size: 9rem;
            color: $accent;
        }

        &__text {
            font-size: 2.5rem;
        }

        &__art {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, -60%);


            width: 6rem;
            
            filter: blur(2px);
        }
    }
}