.application_form {
    display: flex;
    align-items: center;
    flex-direction: column;
    row-gap: 3.5rem;

    width: 45%;
    
    @include respond(phone) {
        width: 100%;
    }

    &__item {
        width: 100%;

        display: flex;
        flex-direction: column;
    }

    label {
        font-size: 3rem;
        margin: 0rem 0rem 2rem 1rem;

        @include respond(phone) {
            text-align: center;
        }
    }

    input {

        &::placeholder {
            font-size: 2rem;
            color: #fff;
            opacity: 0.6;
        }

        font-size: 2rem;
        color: #fff;


        width: 100%;        
        text-align: center;

        padding: 1rem;
    }

    &__button {
        width: fit-content;
        padding: 0.7rem 5rem;

        font-size: $default-font-size;
        color: #fff;
        
        cursor: pointer;

        border: 2px solid $accent;

        transition: all 0.1s linear;
        opacity: 0.8;

        &:hover {
            transform: scale(103%);
            opacity: 1;
        }
    }
}