.chart {
    position: relative;
    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 2rem;

    padding: 3rem;

    height: fit-content;

    

    &__bar_wrapper {
        width: 25rem;
        position: relative;
    }

    &__text {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        display: flex;
        flex-direction: column;
        align-items: center;

        font-size: 2.7rem;

        &__value {
            font-size: $headings-font-size;
        }
    }
}